.new-task-add{border-width:2px;border-color:#52565d;border-style:dashed;border-radius:5px;text-align:center;color:#52565d
}
.new-task-add:hover,.new-task-add.active{background-color:#52565d;color:#fff
}
.new-task-content{display:none;background-color:#35383d
}
.new-task-content.show{display:block
}
.new-task-content .new-task-save{background-color:#4e7dd5;border-radius:5px;text-align:center;color:#fff
}
.comment{background-color:#4d5158;border-radius:5px;font-weight:normal;color:#fff
}
.working-check{border-radius:4px;cursor:pointer;color:#fff;text-align:center;font-size:14px
}
.progress{background-color:#ab0000
