.block-toggle{background-color:#35383d
}
.block-toggle .block-toggle-title{background-size:30px auto;cursor:pointer;color:#fff;-webkit-box-shadow:inset 0 -1px #41444a;box-shadow:inset 0 -1px #41444a
}
.block-toggle .block-toggle-title .title::before{left:0;background-color:#fff;-webkit-mask-image:url(data:image/svg+xml;base64,);mask-image:url(data:image/svg+xml;base64,);-webkit-mask-repeat:no-repeat;content:'';display:inline-block;width:16px;height:16px;position:absolute;top:50%;-webkit-transform:translate(0, -50%);transform:translate(0, -50%)
}
.block-toggle .block-toggle-title:hover,.block-toggle .block-toggle-title.active{background-color:#3c3f45
}
.block-toggle .block-toggle-title.active .new-task-add{border-color:#fff;color:#fff
}
.block-toggle .block-toggle-title.active .title::before{-webkit-mask-image:url(data:image/svg+xml;base64,);mask-image:url(data:image/svg+xml;base64,);-webkit-mask-repeat:no-repeat
}
.block-toggle h3.block-toggle-title{background-color:#4d5158
}
.block-toggle h3.block-toggle-title:hover,.block-toggle h3.block-toggle-title.active{background-color:#52565d
}
.block-toggle h5.block-toggle-title{border-radius:5px;cursor:pointer;background-color:initial
}
.block-toggle h5.block-toggle-title.active,.block-toggle h5.block-toggle-title:hover{background-color:initial
}
.block-toggle.active .block-toggle-title{background-size:30px auto;background-color:#52565d
}
.block-toggle .block-toggle-content{display:none
}
.block-toggle .block-toggle-content.active{display:block
}
